.header {
    width: 100%;
    height: 5rem;
    border-radius: 3rem;
    background-color: #7fffd4;
    margin-top: 1rem;

    display: flex;
    justify-content: flex-start;
    align-items: center;

    &-title {
        padding: 0.5rem;
        font-size: 2rem;
        font-style: italic;
        font-weight: 400;
        color: #0000ff;
    }

    &-logo {
        font-size: 2.5rem;
        padding: 0.5rem;
        color: #ff4500;
        margin-left: 1.5rem;
    }
}
